import React, { Component } from "react";
import {
  Card,
  Table,
  Button,
  Tag,
  Modal,
  Typography,
  message,
  Tooltip,
} from "antd";

import moment from "moment";
import { getArticles, deleteArticleById } from "../../requests";
import XLSX from "xlsx";
window.moment = moment;
const { Column, ColumnGroup } = Table;

const ButtonGroup = Button.Group;

const displayColumns = {
  id: "",
  title: "",
  author: "",
  amount: "",
  createAt: "",
};

export default class Article extends Component {
  constructor() {
    super();
    this.state = {
      dataSource: [
        // {
        //   key: "1",
        //   title: "",
        //   author: "",
        //   createAt: 312312312,
        //   amount: 123,
        // },
        // {
        //   key: "2",
        //   title: "",
        //   author: "",
        //   createAt: 312312312,
        //   amount: 1234,
        // },
      ],

      columns: [
        {
          title: "",
          dataIndex: "title",
          key: "title",
        },
        {
          title: "",
          dataIndex: "author",
          key: "author",
        },
        {
          title: "",
          dataIndex: "createAt",
          key: "createAt",
        },
        {
          title: "",
          dataIndex: "amount",
          key: "amount",
        },
      ],
      total: 0,
      isLoading: false,
      offset: 0,
      pageSize: 10,
      deleteArticleTitle: "",
      isShowArticleModal: false,
      deleteArticleConfirmLoading: false,
      deleteArticleId: null,
    };
  }

  createColumns = (columsKeys) => {
    const columns = columsKeys.map((item) => {
      if (item === "amount") {
        return {
          title: displayColumns[item],
          key: item,
          render: (text, record) => {
            const { amount } = record;
            return (
              <Tooltip title={amount > 200 ? "200" : "200"}>
                <Tag color={amount > 200 ? "red" : "green"}> {amount} </Tag>{" "}
              </Tooltip>
            );
          },
        };
      }
      if (item === "createAt") {
        return {
          title: displayColumns[item],
          key: item,
          render: (text, record) => {
            const { createAt } = record;
            return moment(createAt).format("YYYYMMDD hhmmss");
          },
        };
      }
      return {
        title: displayColumns[item],
        dataIndex: item,
        key: item,
      };
    });
    columns.push({
      title: "",
      dataIndex: "options",
      render: (text, record) => {
        return (
          <ButtonGroup>
            <Button
              type="primary"
              size="small"
              onClick={this.ToEdit.bind(this, record)}
            >
              {" "}
            </Button>{" "}
            <Button
              type="danger"
              size="small"
              onClick={this.ShowArticleDeleted.bind(this, record)}
            >
              {" "}
            </Button>{" "}
          </ButtonGroup>
        );
      },
    });
    return columns;
  };

  onPageChange = (page, pageSize) => {
    this.setState(
      {
        offset: pageSize * (page - 1),
        limited: pageSize,
      },
      () => this.getData()
    );
  };

  onShowSizeChange = (current, size) => {
    this.setState(
      {
        offset: 0,
        limited: size,
      },
      () => this.getData()
    );
  };

  toExcel = () => {
    const data = [Object.keys(this.state.dataSource[0])];
    for (let i = 0; i < this.state.dataSource.length; i++) {
      data.push([
        this.state.dataSource[i].id,
        this.state.dataSource[i].title,
        this.state.dataSource[i].author,
        this.state.dataSource[i].amount,
        moment(this.state.dataSource[i].createAt).format(
          "YYYYMMDD HH:mm:ss"
        ),
      ]);
    }
    //  
    const ws = XLSX.utils.aoa_to_sheet(data);

    /* build new workbook */
    const wb = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(wb, ws, "SheetJS");
    XLSX.writeFile(
      wb,
      `articles-${this.state.offset / this.state.limited + 1}-${moment().format(
        "YYYY-MM-DD hhmmss"
      )}.xlsx`
    );
  };

  // ArticleDeleted = (record) => {
  //   Modal.confirm({
  //     title: "",
  //     content: (
  //       <Typography>
  //         <span style={{ color: "#F00" }}>{record.title}</span>
  //       </Typography>
  //     ),
  //     okText: "",
  //     cancelText: <span style={{ color: "#F00" }}>OMG </span>,
  //   });
  // };

  ToEdit = (record) => {
    this.props.history.push({
      pathname: `/admin/article/edit/${record.id}`,
      state: record,
    });
  };
  ShowArticleDeleted = (record) => {
    this.setState({
      isShowArticleModal: true,
      deleteArticleTitle: record.title,
      deleteArticleId: record.id,
    });
  };

  ArticleDeleted = () => {
    this.setState({
      deleteArticleConfirmLoading: true,
    });
    deleteArticleById(this.state.deleteArticleId)
      .then((resp) => {
        console.log(resp);
        message.success(resp.data.msg);
        // this.getData();
        this.setState(
          {
            offset: 0,
          },
          this.getData()
        );
      })
      .catch((err) => {
        this.setState({
          deleteArticleConfirmLoading: false,
          isShowArticleModal: false,
        });
      })
      .finally(() => {
        this.setState({
          deleteArticleConfirmLoading: false,
          isShowArticleModal: false,
        });
      });
  };

  componentDidMount() {
    this.getData();
  }

  getData = () => {
    this.setState({
      isLoading: true,
    });
    getArticles(this.state.pageSize, this.state.limited)
      .then((resp) => {
        // console.log(resp.data.total);
        const respColumns = this.createColumns(Object.keys(resp.data.list[0]));
        const respList = Object.values(resp.data.list);
        // console.log(respColumns);
        // setState ,
        console.log(this.updater.isMounted(this));
        if (!this.updater.isMounted(this)) return;
        this.setState({
          total: resp.data.total,
          dataSource: respList,
          columns: respColumns,
        });
      })
      .catch((err) => {
        //
      })
      .finally(() => {
        if (!this.updater.isMounted(this)) return;
        this.setState({
          isLoading: false,
        });
      });
  };

  hideDeleteModal = () => {
    this.setState({
      isShowArticleModal: false,
      deleteArticleTitle: "",
      deleteArticleConfireLoading: false,
    });
  };

  render() {
    return (
      <div>
        <Card
          title=""
          extra={<Button onClick={this.toExcel}> Excel </Button>}
        >
          {" "}
          <Table
            loading={this.state.isLoading}
            rowKey={(record) => record.id}
            dataSource={this.state.dataSource}
            columns={this.state.columns}
            pagination={{
              current: this.state.offset / this.state.limited + 1,
              total: this.state.total,
              hideOnSinglePage: true,
              showQuickJumper: true,
              showSizeChanger: true,
              onChange: this.onPageChange,
              onShowSizeChange: this.onShowSizeChange,
            }}
          />
          <Modal
            title=""
            visible={this.state.isShowArticleModal}
            onCancel={this.hideDeleteModal}
            confirmLoading={this.state.deleteArticleConfirmLoading}
            onOk={this.ArticleDeleted}
          >
            <Typography>
              {" "}
              <span
                style={{
                  color: "#f00",
                }}
              >
                {" "}
                {this.state.deleteArticleTitle}{" "}
              </span>
              {" "}
            </Typography>{" "}
          </Modal>{" "}
        </Card>{" "}
      </div>
    );
  }
}
